}
.gradient-background {
  background-color: #ffffff;
  background-image: radial-gradient(at 0% 0%, #ffeef8 0px, transparent 50%),
    radial-gradient(at 100% 0%, #eefaff 0px, transparent 50%),
    radial-gradient(at 100% 100%, #fffbe6 0px, transparent 50%),
    radial-gradient(at 0% 100%, #f0f7ff 0px, transparent 50%);
  background-size: 150% 150%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  animation: background-float 20s ease infinite;
}
.chat-scroll-container {
  flex: 1;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: thin;
  scrollbar-color: #000000 rgba(0, 0, 0, 0.05);
}
.chat-scroll-container::-webkit-scrollbar {
  width: 4px;
}
.chat-scroll-container::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 2px;
}
.chat-scroll-container::-webkit-scrollbar-thumb {
  background: #000000;
  border-radius: 10px;
}
.formula-gradient {
  background: linear-gradient(90deg, #70a1ff, #ff7eb3, #ffcc33);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 900;
  letter-spacing: -0.05em;
}
.glass-bottom-tray {
  background: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(25px);
  -webkit-backdrop-filter: blur(25px);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}
@keyframes gradient-loop {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.animate-gradient-loop {
  animation: gradient-loop 3s ease infinite;
}
@keyframes rotate-slow {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.rotate-slow {
  animation: rotate-slow 20s linear infinite;
}
@keyframes background-float {
  0% {
    background-position: 0% 0%;
  }
  50% {
    background-position: 100% 100%;
  }
  100% {
    background-position: 0% 0%;
  }
}
